body {
  background: #1e1e1e;
  font-family: "Gilroy";
}

* {
  -webkit-tap-highlight-color: transparent; /* Убирание эффекта активного состояния при нажатии */
}

.none {
  display: none !important;
}

html {
  touch-action: pan-y;
  -ms-content-zooming: none;
  -ms-user-select: none;
  -webkit-user-scalable: none; /* Отключение зума на мобильных устройствах */
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
